@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;600&display=swap);
*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%;--orange: #E07A5F;--dark-blue: #3D405B;--green: #81B29A;--champagne: #F2CC8F;--eggshell: #F4F1DE}@media only screen and (max-width: 56em){html{font-size:56.25%}}@media only screen and (max-width: 35em){html{font-size:50%}}img{display:block}body{box-sizing:border-box;font-size:1.6rem;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;width:100%;overflow-x:hidden;font-family:'Quicksand', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.category{--bg-color: var(--green);min-height:100vh;background-color:var(--bg-color);padding:12rem 4rem;display:flex;flex-direction:column;justify-content:center;align-items:center}.category--media{--bg-color: var(--dark-blue)}.category--content{--bg-color: var(--orange)}.category__container{display:grid;grid-template-columns:repeat(auto-fit, minmax(25rem, 1fr));justify-items:center;grid-gap:4rem;width:100%}.category__item{padding:4rem;max-width:35rem;background-color:#fff;border:none;font-family:inherit;display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;box-shadow:0px 6px 11px 5px rgba(0,0,0,0.1);border-radius:1rem;cursor:pointer;transition:transform .3s ease, box-shadow .3s ease}.category__item:hover{transform:translateY(-3px);box-shadow:0px 6px 20px 20px rgba(0,0,0,0.08)}.category__item:focus{outline:none;transform:translateY(1px);box-shadow:0px 5px 8px 4px rgba(0,0,0,0.12)}.category__item h2{font-weight:500;font-size:3.2rem}.category__item img{max-width:100%}

.navbar{position:absolute;color:#fff;padding:4rem 5rem}@media only screen and (max-width: 35em){.navbar{padding:3rem}}.navbar__brand{font-size:3.2rem}

